<!-- src/components/UserList.vue -->
<template>
  <div>
    <h1>Users</h1>
    <table>
      <thead>
        <tr>
          <th>Id</th>
          <th>Name</th>
          <th>Age</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="user in users" :key="user.id">
          <td>{{ user.id }}</td>
          <td>{{ user.name }}</td>
          <td>{{ user.age }}</td>
          <td>
            <router-link :to="`/users/${user.id}`">Voir</router-link>
            <router-link :to="`/users/${user.id}/edit`">Modifier</router-link>
            <router-link :to="`/users/${user.id}/destroy`">Supprimer</router-link>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="pagination">
      <button @click="fetchUsers(links.prev)" :disabled="!links.prev">Previous</button>
      <button @click="fetchUsers(links.next)" :disabled="!links.next">Next</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'UserList',
  data() {
    return {
      users: [],
      links: {}
    };
  },
  created() {
    this.fetchUsers('http://localhost:3000/users?page=1&limit=3');
  },
  methods: {
    fetchUsers(url) {
      axios.get(url)
        .then(response => {
          this.users = response.data.data;
          this.links = response.data.links;
          console.log("users",this.users)
        })
        .catch(error => {
          console.error('There was an error fetching the users!', error);
        });
    }
  }
};
</script>